import React from 'react';
import {OutTable, ExcelRenderer} from 'react-excel-renderer';

interface State {
    cols:any[]
    rows:any[]
}

interface Props {
    stepChange: (step: number) => void
}

class ViewExcel extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            cols:[],
            rows:[]
        }
        this.onClear = this.onClear.bind(this)
        this.onUploadFile = this.onUploadFile.bind(this)
    }

    componentDidMount() {
        let emails = localStorage.getItem('emails');
        if(emails)
            this.setState(JSON.parse(emails));
    }

    onClear(e:any){
        this.setState({cols:[],rows:[]})
        localStorage.removeItem('emails');
        this.props.stepChange(1)
    }

    async onUploadFile(event:any) {
        let fileObj = event.target.files[0];
        ExcelRenderer(fileObj, (err: any, resp: any) => {
            if (err) {
                console.log(err);
            } else {
                this.setState({
                    cols: resp.cols,
                    rows: resp.rows
                });
                localStorage.setItem('emails', JSON.stringify({cols: resp.cols,rows: resp.rows}));
                if (resp.rows.length > 1)
                    this.props.stepChange(2)
            }
        });
    }

    render() {
        if(this.state.rows.length > 0) {
            let row = this.state.rows[0];
            return (
                <div className="card-body">
                    <i className="far fa-trash-alt pull-right pointer mt50" onClick={this.onClear} />
                    <div className="tableFixHead">
                    <table className="table">
                        <thead>
                        <tr>
                            {this.state.cols.map((item, index) => (
                                <th scope="col" key={index}>{row[index]}</th>
                            ))}
                        </tr>
                        </thead>
                        <tbody>
                        {this.state.rows.map((row, rowIndex) => (
                            rowIndex > 0 &&
                            <tr key={rowIndex}>
                                {this.state.cols.map((item, index) => (
                                <td scope="col" key={index}>{row[index]}</td>
                                ))}
                            </tr>
                        ))}

                        </tbody>
                    </table>
                    </div>
                </div>
            )
        }
        return (
            <div className="card-body">
                <p className="card-text">Carga la lista de email desde un fichero excel.</p>
                <div>
                    <input type="file" name="file" id="fileExcel" onChange={this.onUploadFile} accept="application/*" className="hidden"/>
                    <div className="btn-group-toggle">
                        <label htmlFor="fileExcel" className="btn btn-outline-primary w-100 ">
                            <i className="fas fa-upload mr-1"/>Cargar datos
                        </label>
                    </div>
                </div>
            </div>
        )
    }
}

export default ViewExcel;
