import React from 'react';
import IframeContainer from "../common/IframeContainer";
import DC from "../common/DataConector"
interface State {
    tab:string,
    template:string
}

interface Props {
    stepChange: (step: number) => void
}

class ViewTemplate extends React.Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            tab:'preview',
            template: ''
        }
        this.onTabClick = this.onTabClick.bind(this)
    }

    async componentDidMount() {
        const result = await DC.Get('./template.html');
        this.setState({template:result})
    }

    onTabClick(e: any) {
        e.preventDefault();
        this.setState({tab: e.currentTarget.dataset.tab})
    }

    render() {
        return (
            <div className="card-body">
                <p className="card-text">AÃ±ade columnas con {'{{COL_NAME}}'}</p>
                <ul className="nav nav-tabs">
                    <li className="nav-item" data-tab="preview" onClick={this.onTabClick}>
                        <a className={"nav-link" + (this.state.tab == 'preview' ? " active" : "")} href="#"
                        >Preview</a>
                    </li>
                    <li className="nav-item" data-tab="html" onClick={this.onTabClick}>
                        <a className={"nav-link" + (this.state.tab == 'html' ? " active" : "")} href="#"
                        >Html</a>
                    </li>
                </ul>
                <div className="tab-content" id="myTabContent">
                    <div className={"tab-pane fade" + (this.state.tab == 'preview' ? " show active" : "")}>
                        <IframeContainer className="" body={this.state.template} css="" />
                    </div>
                    <div className={"tab-pane fade" + (this.state.tab == 'html' ? " show active" : "")}>
                        <textarea className="html" defaultValue={this.state.template}/>
                    </div>
                </div>
            </div>
        )
    }
}

export default ViewTemplate;
