import React from 'react';
import ViewExcel from "./ViewExcel"
import ViewTemplate from "./ViewTemplate";
import ViewSender from "./ViewSender";

interface State {
  isLoading: boolean,
  step:number
}

interface Props {
}

class App extends React.Component<Props, State> {
  constructor(props: Props) {
    super(props);
    this.state = {
      isLoading: true,
      step: 3
    }
    this.stepChange = this.stepChange.bind(this)
  }

  stepChange(step:number){
    this.setState({step:step})
  }

  render() {
    return (
        <div className="container">
          <header className="blog-header py-3">
            <div className="row flex-nowrap justify-content-between align-items-center">
              <div className="col-4 pt-1">
              </div>
              <div className="col-4 text-center">
                <a className="blog-header-logo text-dark" href="#">Mails Sender</a>
              </div>
              <div className="col-4 d-flex justify-content-end align-items-center">
              </div>
            </div>
          </header>

          <main role="main" className="container">
            <br/>
            <div className="card">
              <div className="card-header">
                1 - Lista de email
              </div>
                <ViewExcel stepChange={this.stepChange}/>
            </div>

            <div className="card mt-5">
              <div className="card-header">
                2 - Template / Plantilla
              </div>
              {this.state.step > 1 && <ViewTemplate stepChange={this.stepChange}/> }
            </div>

            <div className="card mt-5 mb-5">
              <div className="card-header">
                3 - Enviar con Mailjet
              </div>
              {this.state.step > 2 && <ViewSender stepChange={this.stepChange} /> }
            </div>
            <br/>
          </main>
        </div>
    )
  }
}

export default App;
