import React from 'react';
interface State {
}

interface Props {
    className:string
    body:string
    css:string
}


class IframeContainer extends React.Component<Props, State> {
    iframe:any
    constructor(props: Props) {
        super(props);
        this.iframe = React.createRef();
        this.refreshIframe = this.refreshIframe.bind(this)
    }

    async componentDidMount() {
        this.refreshIframe();
    }

    componentDidUpdate() {
        this.refreshIframe();
    }

    refreshIframe() {
        let body = this.props.body;
        let css = this.props.css;
        const iframe = this.iframe.current;
        const document = iframe.contentDocument;
        document.body.innerHTML =  body;
        try {
            iframe.onload = function (obj: any) {
                if(obj.contentWindow)
                obj.style.height = obj.contentWindow.document.documentElement.scrollHeight + 'px';
            };
        }catch (e) {
            
        }
        const head = document.getElementsByTagName('head')[0];

        let style = document.createElement('style');
        style.innerHTML = css;
        head.appendChild(style);

    }

    render() {
        return <iframe className={this.props.className} ref={this.iframe} />
    }
}


export default IframeContainer